<template>
  <div class="app-container">
    <page-title first-title="版本列表"></page-title>

    <div class="btn-wrapper">
      <el-button type="primary" @click="addVersion">新增</el-button>
    </div>

    <div class="list-wrapper">
      <el-table :data="versionData" v-loading="loading" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column label="更新类型" prop="type" :formatter="fmtType"></el-table-column>
        <el-table-column label="更新状态" prop="status" :formatter="fmtStatus"></el-table-column>
        <el-table-column label="apk包名" prop="apk_name"></el-table-column>
        <el-table-column label="apk包地址" prop="apk_url"></el-table-column>
        <el-table-column label="包大小" prop="apk_size"></el-table-column>
        <el-table-column label="版本号" prop="version_name"></el-table-column>
        <el-table-column label="版本码" prop="version_code"></el-table-column>
        <el-table-column label="图片路径" prop="update_image"></el-table-column>
        <el-table-column label="标题" prop="update_title"></el-table-column>
        <el-table-column label="更新内容" prop="update_content"></el-table-column>
        <el-table-column label="升级版本" prop="update_version"></el-table-column>
        <el-table-column label="安卓md5" prop="apk_md5"></el-table-column>
        <el-table-column label="iOS升级的url" prop="update_url"></el-table-column>
        <el-table-column label="添加时间" prop="add_time" :formatter="fmtTime"></el-table-column>
        <el-table-column label="修改时间" prop="update_time" :formatter="fmtTime"></el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button type="text" @click="goEdit(scope.row.id)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="pagination-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        background
        layout="prev, pager, next, jumper"
        :total="total"
        :hide-on-single-page="true"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getVersionList } from '../../api/appversion'
import { TYPE, STATUS } from './static'
export default {
  name: 'VersionList',
  data() {
    return {
      versionData: [],
      loading: false,
      currentPage: 1,
      total: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      getVersionList(this.currentPage)
        .then(
          res => {
            this.versionData = res.list
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
        .finally(() => {
          this.loading = false
        })
    },
    addVersion() {
      this.$router.push({ name: 'AddandEditVer' })
    },
    goEdit(id) {
      this.$router.push({ name: 'AddandEditVer', query: { id: id } })
    },
    handleCurrentChange() {
      this.getList()
    },
    fmtType(row, column, cellValue) {
      let name = ''
      TYPE.forEach(item => {
        if (item.id == cellValue) {
          name = item.name
        }
      })
      return name
    },
    fmtStatus(row, column, cellValue) {
      let name = ''
      STATUS.forEach(item => {
        if (item.id == cellValue) {
          name = item.name
        }
      })
      return name
    },
    fmtTime(row, column, cellValue) {
      if (cellValue == 0) return '无'
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    }
  }
}
</script>

<style lang="scss" scoped>
.btn-wrapper,
.list-wrapper {
  margin-top: 20px;
}
.btn-wrapper {
  text-align: right;
}
</style>
